@import url(https://fonts.googleapis.com/css2?family=Italianno&display=swap);
* {
  user-select: none;
  -ms-user-select: none;
  outline: 0;
  margin: 0;
  padding: 0;
  -webkit-tab-highlight-color: rgba(0, 0, 0, 0);
}

html {
  width: 100%;
  height: 100%;
}

body {
  cursor: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  background-color: rgb(3, 5, 44);
  font-family: 'Italianno', cursive;
}

a {
  color: var(--system-text);
  text-decoration: none;
  cursor: none;
}

canvas {
  width: 100%;
  height: 100%;
}

.hidden {
  display: none;
}

.visuallyhidden {
  opacity: 0;
}

.instruction {
  position: absolute;
  fill: #f2eef9;
  transition: all 0.5s ease;
}

.instruction:after {
  content: "Tap anywhere";
  color: #f2eef9;
  font-size: 30px;
}

.instruction__svg {
  height: 96px;
  width: 96px;
}

.cursor {
  position: absolute;
  color: #f2eef9;
  border-radius: 100%;
  border: 5px solid #f2eef9;
  height: 20px;
  width: 20px;
  z-index: 9999;
  pointer-events: none;
}

.back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
  animation-name: animatebottom;
  animation-duration: 0.4s;
}

.back-btn__link {
  fill: #f2eef9;
  color: #f2eef9;
  transition: all 0.5s ease;
}

.back-btn__link:hover {
  fill: rgb(142, 142, 147);
  color: rgb(142, 142, 147);
}

.back-btn__svg {
  height: 24px;
  width: 24px;
}

.back-btn__text {
  padding: 5px;
  vertical-align: top;
  font-size: 30px;
  font-weight: 600;
}

@keyframes animatebottom {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

